          font-size: pr(30);
          margin-top: pr(10);
          margin-right: pr(10);
        }

      }
    }

    .money-par {
      position: absolute;
      top: 0;
      right: 0;
      width: pr(100);
      height: pr(50);
      font-size: pr(24);
      color: #fff;
      background: rgba(255, 255, 255, 0.5);
      border-radius: 0 0 0 pr(10);

      .time {
        width: pr(25);
        color: #ff0000;
        font-weight: 600;
        margin-right: pr(5);

        &.hidden {
          opacity: 0;
        }

        &.move {
          -webkit-animationn: timeMove 1s infinite;
          -moz-animation: timeMove 1s infinite;
          -o-animation: timeMove 1s infinite;
          animation: timeMove 1s infinite;

          @keyframes timeMove{
            0% {
              font-size: pr(24);
            }
            50% {
              font-size: pr(40);
            }
            100% {
              font-size: pr(24);
            }
          }
        }

      }

      .grade-count {
        .abc-img {
          width: pr(30);
          height: pr(30);
        }
      }

    }

  }

</style>





